import { Table, Button, Space, Popconfirm, message } from "antd";
import React, {
  useEffect,
  useState,
  forwardRef,
  useImperativeHandle,
} from "react";
import dayjs from "dayjs";
import { mock } from "@/api/mock";
import { account_get, get_by_user_nam } from "../../../api/acount";
import { account_log_get, role_log_get } from "../../../api/log";
import { get_id_user, repacle_id_user_name } from "../../../utils";
import { room_del, room_get } from "../../../api/room";
// import mock from ''
const ListTable = (props, ref) => {
  useImperativeHandle(ref, () => ({
    // changeVal 就是暴露给父组件的方法
    getData: (newVal) => {
      room_get_handle(newVal);
    },
  }));
  const { update_room_h, pageSize = 5, total = 20, showTotal = true } = props;
  const [loading, setLoading] = useState(false);
  const [data, setData] = useState([]);
  const [pagination, setPagination] = useState({
    current: 1,
    pageSize: pageSize || 20,
    total: total || 0,
  });
  const columns = [
    { title: "房间id", dataIndex: "id", key: "id", fixed: "left", width: 100 },
    { title: "玩法类型说明", dataIndex: "module" },
    { title: "资源类型", dataIndex: "action" },
    { title: "最大玩家数", dataIndex: "action_user" },
    { title: "底注", dataIndex: "action_user" },
    { title: "分数限制", dataIndex: "action_user" },
    { title: "覆盖区域", dataIndex: "action_user" },
    { title: "玩家最低带入", dataIndex: "action_user" },
    { title: "玩家最高带入", dataIndex: "action_user" },
    { title: "初始弃牌分数", dataIndex: "action_user" },
    { title: "中途弃牌分数", dataIndex: "action_user" },
    { title: "扣分下限", dataIndex: "action_user" },
    { title: "扣分上限", dataIndex: "action_user" },
    { title: "最大超时弃牌次数", dataIndex: "action_user" },
    { title: "牌副数", dataIndex: "action_user" },
    { title: "每副牌使用Joker数量", dataIndex: "action_user" },
    { title: "手牌张数", dataIndex: "action_user" },
    { title: "创建时间", dataIndex: "action_user" },
    { title: "状态", dataIndex: "action_user" },
    {
      title: "操作",
      dataIndex: "action_remarks",
      fixed: "right",
      width: 150,
      render: (text, record) => (
        <Space size="middle">
          <a
            onClick={() => {
              update_room_h();
            }}
          >
            编辑
          </a>
          <Popconfirm
            title="确定删除这条数据?"
            onConfirm={() => {}}
            okText="Yes"
            cancelText="No"
          >
            <a
              onClick={() => {
                delte_room_h(record.id);
              }}
            >
              删除
            </a>
          </Popconfirm>
        </Space>
      ),
    },
  ];
  //删除房间列表
  const delte_room_h = async (id) => {
    try {
      await room_del({ id });
      message.success("删除成功");
      if  ( (pagination.total - 1) / pagination.pageSize < pagination.current &&
  pagination.current != 1) {
        setPagination({
          ...pagination,
          current: pagination.current - 1,
        });
      } else {
        room_get_handle();
      }
    } catch (error) {
      console.error(error);
    }
  };
  //获取房间列表
  const room_get_handle = async (params = {}) => {
    try {
      setLoading(true);
      let data = await room_get({
        page: pagination.current,
        page_size: pagination.pageSize,
        ...params,
      });
      // if (data.data.length > 0) {
      //   const user_ids = get_id_user(data.data, { key: "action_user" });
      //   const ids_names_data = await get_by_user_nam({
      //     user_ids: user_ids.toString(),
      //   });
      //   let ids_names = ids_names_data.data;
      //   data.data = repacle_id_user_name(data.data, {
      //     key: "action_user",
      //     data: ids_names,
      //     trans_key: "action_user_name",
      //   });
      // }
      setData(data.data);
      setPagination({
        ...pagination,
        total: data.page.count,
        pageSize: data.page.page_size,
      });
      setLoading(false);
    } catch (e) {
      setLoading(false);
      console.error(e);
    }
  };
  //表格改变触发
  const onchange = (pagination) => {
    setPagination({ ...pagination });
  };
  useEffect(async () => {
    room_get_handle();
  }, [pagination.current]);
  return (
    <>
      <Table
        bordered
        loading={loading}
        columns={columns}
        dataSource={data}
        scroll={{ x: 3300 }}
        rowKey="id"
        pagination={pagination}
        onChange={onchange}
      ></Table>
    </>
  );
};
export default forwardRef(ListTable);
